*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
ul,
ol,
li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
html,
body {
  height: 100%;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  color: #111;
  background: #f5f5f5;
}

@import '../../../styles/breakpoints.scss';
@import '../../../styles/mixins.scss';

.App {
  max-width: 550px;
  min-width: 230px;
  height: 100%;
  margin: 0 auto;
}

.title {
  margin: 20px auto;
  font-size: 80px;
  font-weight: 200;
  text-align: center;
  color: #b83f45;
}

.todo {
  position: relative;
  max-width: 550px;
  background-color: #fff;
  box-shadow:
    0 2px 4px 0 rgba(0, 0, 0, 0.2),
    0 25px 50px 0 rgba(0, 0, 0, 0.1);
}
