*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
ul,
ol,
li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
html,
body {
  height: 100%;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  color: #111;
  background: #f5f5f5;
}

@import '../../../../styles/breakpoints.scss';
@import '../../../../styles/mixins.scss';

.allBtn {
  position: absolute;
  top: 8px;
  left: 8px;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  font-size: 24px;
  color: #949494;
  background-color: rgba(0, 0, 0, 0.003);
  cursor: pointer;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);

  &.completed {
    color: #484848;
  }
}

.mainInput {
  width: 100%;
  height: 65px;
  padding: 16px 16px 16px 60px;
  font-size: 24px;
  background-color: rgba(0, 0, 0, 0.003);
  box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03);
  outline: none;

  &:focus {
    box-shadow: 0 0 2px 2px #cf7d7d;
  }
}

@include respondToMobile {
  .allBtn {
    top: 6px;
    left: 13px;
    width: 38px;
    height: 38px;
  }
  .mainInput {
    height: 50px;
    font-size: 18px;
  }
}
