*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
ul,
ol,
li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
html,
body {
  height: 100%;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  color: #111;
  background: #f5f5f5;
}

@import '../../../../styles/breakpoints.scss';
@import '../../../../styles/mixins.scss';

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 45px;
  padding: 10px 15px;
  font-size: 15px;
  border-top: 1px solid #e6e6e6;
}

.footer:before {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 50px;
  box-shadow:
    0 1px 1px rgba(0, 0, 0, 0.2),
    0 8px 0 -3px #f6f6f6,
    0 9px 1px -3px rgba(0, 0, 0, 0.2),
    0 16px 0 -6px #f6f6f6,
    0 17px 2px -6px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.footerMain {
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 70%;
}

.footerCount {
  margin-right: 5px;
  cursor: default;
}

.footerFilterBtn {
  margin-right: 5px;
  padding: 5px 7px;
  font-size: 15px;
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: 3px;
  cursor: pointer;
  outline: none;

  &:not(:last-child) {
    margin-right: 5px;
  }

  &:hover {
    border: 1px solid #cf7d7d;
  }

  &:focus {
    box-shadow: 0 0 2px 2px #cf7d7d;
  }
}

.footerFilters .active {
  border: 1px solid #ce4646;
}

.footerClear {
  z-index: 99;
  padding: 2px 1px;
  font-size: 15px;
  background-color: transparent;
  border: 1px solid transparent;
  cursor: pointer;
  outline: none;

  &:hover {
    text-decoration: underline;
  }

  &:focus {
    box-shadow: 0 0 2px 2px #cf7d7d;
  }
}

@include respondToMobile {
  .footer {
    padding: 10px 5px;
    font-size: 12px;
  }
  .footerFilterBtn {
    margin-right: 2px;
    padding: 5px 4px;
    font-size: 12px;
  }
  .footerClear {
    font-size: 12px;
  }
}
